import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

interface ChoiceQuestion {
  id: number;
  type: 'choice';
  question: string;
  options: string[];
  correctAnswer: number;
  explanation: string;
  category: string;
}

interface CalculationQuestion {
  id: number;
  type: 'calculation';
  question: string;
  correctAnswer: number;
  unit: string;
  explanation: string;
  category: string;
  tolerance?: number; // 
}

interface OpenQuestion {
  id: number;
  type: 'open';
  question: string;
  explanation: string;
  category: string;
}

type Question = ChoiceQuestion | CalculationQuestion | OpenQuestion;

// 
const questions: Question[] = [
  //  1: 
  {
    id: 1,
    type: 'choice',
    question: '2kg50N1m',
    options: [
      'A. 3 rad/s',
      'B. 4 rad/s', 
      'C. 5 rad/s',
      'D. 6 rad/s'
    ],
    correctAnswer: 2, // C
    explanation: ' F = mrF = 50N50 = 2    1  = 25 = 5 rad/s',
    category: ''
  },
  
  //  2: 
  {
    id: 2,
    type: 'choice',
    question: 'vr',
    options: [
      'A. v = /r',
      'B. v = r',
      'C. v = r',
      'D. v = r/'
    ],
    correctAnswer: 1, // B
    explanation: 'v = r',
    category: ''
  },
  
  //  3: 
  {
    id: 3,
    type: 'choice',
    question: '',
    options: [
      'A. ',
      'B. ',
      'C. ',
      'D. '
    ],
    correctAnswer: 2, // C
    explanation: '',
    category: ''
  },
  
  //  1: 
  {
    id: 4,
    type: 'calculation',
    question: '0.5kg0.8m20N',
    correctAnswer: 5.66,
    unit: 'm/s',
    explanation: 'F = mv/r20 = 0.5  v / 0.8 v = (20  0.8 / 0.5) = 32  5.66 m/s',
    category: '',
    tolerance: 0.1
  },
  
  //  2: 
  {
    id: 5,
    type: 'calculation',
    question: '1.510m3652',
    correctAnswer: 1.99e-7,
    unit: 'rad/s',
    explanation: '  = 2/TT = 365243600 = 31536000s = 2/31536000  1.9910 rad/s',
    category: '',
    tolerance: 0.05e-7
  },
  
  //  3: 
  {
    id: 6,
    type: 'calculation',
    question: '1000kg72km/h50mg10m/s',
    correctAnswer: 8000,
    unit: 'N',
    explanation: 'v = 72km/h = 20m/smg - N = mv/rN = mg - mv/r = 100010 - 100020/50 = 10000 - 8000 = 2000N',
    category: '',
    tolerance: 100
  },
  
  // : 
  {
    id: 7,
    type: 'open',
    question: '',
    explanation: '',
    category: ''
  }
];

const QuizPage: React.FC = () => {
  const navigate = useNavigate();
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [userAnswers, setUserAnswers] = useState<Record<number, any>>({});
  const [showResults, setShowResults] = useState(false);
  const [uploadedFiles, setUploadedFiles] = useState<Record<number, File[]>>({});

  const currentQuestion = questions[currentQuestionIndex];
  const progress = ((currentQuestionIndex + 1) / questions.length) * 100;

  const handleChoiceAnswer = (questionId: number, choiceIndex: number) => {
    setUserAnswers(prev => ({ ...prev, [questionId]: choiceIndex }));
  };

  const handleCalculationAnswer = (questionId: number, value: string) => {
    setUserAnswers(prev => ({ ...prev, [questionId]: value }));
  };

  const handleOpenAnswer = (questionId: number, value: string) => {
    setUserAnswers(prev => ({ ...prev, [questionId]: value }));
  };

  const handleFileUpload = (questionId: number, files: FileList | null) => {
    if (files) {
      setUploadedFiles(prev => ({ 
        ...prev, 
        [questionId]: Array.from(files) 
      }));
    }
  };

  const nextQuestion = () => {
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
    } else {
      setShowResults(true);
    }
  };

  const prevQuestion = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(prev => prev - 1);
    }
  };

  const calculateScore = () => {
    let correct = 0;
    questions.forEach(question => {
      if (question.type === 'choice') {
        const userAnswer = userAnswers[question.id];
        if (userAnswer === question.correctAnswer) {
          correct++;
        }
      } else if (question.type === 'calculation') {
        const userAnswer = parseFloat(userAnswers[question.id]);
        const correctAnswer = question.correctAnswer;
        const tolerance = question.tolerance || 0.1;
        if (Math.abs(userAnswer - correctAnswer) <= tolerance) {
          correct++;
        }
      }
    });
    return correct;
  };

  const renderQuestion = (question: Question) => {
    switch (question.type) {
      case 'choice':
        return (
          <div className="space-y-4">
            <div className="bg-blue-50 px-3 py-1 rounded-full inline-block">
              <span className="text-blue-700 text-sm font-medium">{question.category}</span>
            </div>
            <h3 className="text-lg font-semibold text-gray-800">{question.question}</h3>
            <div className="space-y-3">
              {question.options.map((option, index) => (
                <label key={index} className="flex items-center p-3 border rounded-lg cursor-pointer hover:bg-gray-50 transition-colors">
                  <input
                    type="radio"
                    name={`question-${question.id}`}
                    value={index}
                    checked={userAnswers[question.id] === index}
                    onChange={() => handleChoiceAnswer(question.id, index)}
                    className="mr-3 text-blue-600"
                  />
                  <span className="text-gray-700">{option}</span>
                </label>
              ))}
            </div>
          </div>
        );

      case 'calculation':
        return (
          <div className="space-y-4">
            <div className="bg-green-50 px-3 py-1 rounded-full inline-block">
              <span className="text-green-700 text-sm font-medium">{question.category}</span>
            </div>
            <h3 className="text-lg font-semibold text-gray-800">{question.question}</h3>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  {question.unit}
                </label>
                <input
                  type="number"
                  step="any"
                  value={userAnswers[question.id] || ''}
                  onChange={(e) => handleCalculationAnswer(question.id, e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder=""
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <input
                  type="file"
                  multiple
                  accept=".jpg,.jpeg,.png,.pdf,.doc,.docx"
                  onChange={(e) => handleFileUpload(question.id, e.target.files)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                {uploadedFiles[question.id] && (
                  <div className="mt-2 text-sm text-gray-600">
                     {uploadedFiles[question.id].length} 
                  </div>
                )}
              </div>
            </div>
          </div>
        );

      case 'open':
        return (
          <div className="space-y-4">
            <div className="bg-purple-50 px-3 py-1 rounded-full inline-block">
              <span className="text-purple-700 text-sm font-medium">{question.category}</span>
            </div>
            <h3 className="text-lg font-semibold text-gray-800">{question.question}</h3>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <textarea
                  value={userAnswers[question.id] || ''}
                  onChange={(e) => handleOpenAnswer(question.id, e.target.value)}
                  rows={6}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="..."
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <input
                  type="file"
                  multiple
                  accept=".jpg,.jpeg,.png,.pdf,.doc,.docx"
                  onChange={(e) => handleFileUpload(question.id, e.target.files)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                {uploadedFiles[question.id] && (
                  <div className="mt-2 text-sm text-gray-600">
                     {uploadedFiles[question.id].length} 
                  </div>
                )}
              </div>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  if (showResults) {
    const score = calculateScore();
    const totalChoiceAndCalc = questions.filter(q => q.type === 'choice' || q.type === 'calculation').length;

    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-6">
        <div className="bg-white rounded-lg shadow-lg p-8 max-w-2xl w-full">
          <div className="text-center">
            <div className="mb-6">
              <div className="w-20 h-20 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-2xl font-bold text-blue-600">{score}/{totalChoiceAndCalc}</span>
              </div>
              <h2 className="text-2xl font-bold text-gray-800 mb-2"></h2>
              <p className="text-gray-600"> {score} </p>
            </div>
            
            <div className="space-y-4 mb-8">
              <div className="bg-blue-50 p-4 rounded-lg">
                <h3 className="font-semibold text-blue-800 mb-2"></h3>
                <ul className="text-sm text-blue-700 space-y-1">
                  <li>  F = mr = mv/r</li>
                  <li> </li>
                  <li> </li>
                  <li> </li>
                </ul>
              </div>
            </div>
            
            <div className="flex space-x-4">
              <button
                onClick={() => {
                  setShowResults(false);
                  setCurrentQuestionIndex(0);
                  setUserAnswers({});
                  setUploadedFiles({});
                }}
                className="flex-1 px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-medium"
              >
                
              </button>
              <button
                onClick={() => navigate('/')}
                className="flex-1 px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors font-medium"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/*  */}
      <div className="bg-white shadow-sm">
        <div className="max-w-4xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between mb-2">
            <h1 className="text-2xl font-bold text-gray-800"></h1>
            <span className="text-sm text-gray-600">
               {currentQuestionIndex + 1}  /  {questions.length} 
            </span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-2">
            <div 
              className="bg-blue-600 h-2 rounded-full transition-all duration-300"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>
      </div>

      {/*  */}
      <div className="max-w-4xl mx-auto px-6 py-8">
        <div className="bg-white rounded-lg shadow-lg p-8">
          {renderQuestion(currentQuestion)}
          
          {/*  */}
          <div className="flex justify-between items-center mt-8 pt-6 border-t border-gray-200">
            <button
              onClick={prevQuestion}
              disabled={currentQuestionIndex === 0}
              className={`px-6 py-2 rounded-lg font-medium transition-colors ${
                currentQuestionIndex === 0
                  ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                  : 'bg-gray-600 text-white hover:bg-gray-700'
              }`}
            >
              
            </button>
            
            <div className="flex space-x-4">
              <button
                onClick={() => navigate('/')}
                className="px-6 py-2 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300 transition-colors font-medium"
              >
                
              </button>
              <button
                onClick={nextQuestion}
                className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-medium"
              >
                {currentQuestionIndex === questions.length - 1 ? '' : ''}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default QuizPage;